import { Router } from 'dva/router'
// import { Router, Route } from 'dva/router'
import IndexPage from './routes/IndexPage'
// import OverView from './overview'
// import Console from './console'
import Layout from './index/layout'

export default function ({ browserHistory, app, user }) {
  const routes = [
    {
      path: '/',
      component: Layout,
      indexRoute: {
        onEnter: (nextState, replace) => {
          if (user.firstPart) {
            replace({
              pathname: '/overview/mine/workbench',
              search: '?archived=false&finished=false',
            })
          } else {
            replace({
              pathname: '/overview/projects',
            })
          }
        },
      },
      childRoutes: [
        {
          path: 'overview',
          getComponent(location, cb) {
            System.import('./index/overview').then(module => cb(null, module.default))
          },
          indexRoute: {
            onEnter: (nextState, replace) => {
              replace({
                pathname: '/overview/projects',
              })
            },
          },
          childRoutes: [
            ...OverView(app),
          ],
        },
        {
          path: 'console/project/:projectId',
          getComponent(location, cb) {
            System.import('./index/console').then(module => cb(null, module.default))
          },
          childRoutes: [
            ...Console(app),
          ],
        },
      ],
    },
  ]
  return <Router history={browserHistory} routes={routes} />
}

